<script>
import { Button, createResource } from 'frappe-ui'
import DataTable from '../components/Data-table.vue'
import Tabs from '../components/tabs.vue'
import ImageSlider from '../components/image-slider.vue'
import Details from '../components/ProjectDetails/index.vue'
import Documents from '../components/documents.vue'
import Location from '../components/Location.vue'
import projectdetail from '../data/projectdetails.json';
import StockList from '../components/ProjectDetails/StockList.vue'
import StageList from '../components/ProjectDetails/StageList.vue'
export default {
  name: 'ProjectDetail',
  components: {
    ImageSlider,
    Details,
    Tabs,
    DataTable,
    Button,
    Documents,
    Location,
    StockList,
    StageList
},
  data() {
    return {
      available: '',
      sold: '',
      reserved: '',
      images: [],
      totalAllocation: '',
      project_detail: {},
      tabs: projectdetail.tabs,
    }
  },
  methods: {
    getdata() {
      const data = createResource({
        method: 'Get',
        url: `opssalesportal.api.realestate_project.get_project_details?project=${this.$route.params.id}`
      })
      return data.reload()
    }
  },
  async mounted() {
    const response = await this.getdata()
    this.project_detail = response.projectdetails
    this.images = [{ 'image': this.project_detail.featured_image }, ...this.project_detail.project_images]
    this.available = response.availabel
    this.reserved = response.reserved
    this.sold = response.sold
    this.totalAllocation = response.totalAllocation
  }
}
</script>